                 <template slot=\"footer\">\n                            <span class=\"text-danger mr-2\">5.72%</span>\n                            <span class=\"text-nowrap\">Since last month</span>\n                        </template>\n                    </stats-card>\n\n                </b-col>\n                <b-col xl=\"3\" md=\"6\">\n                    <stats-card title=\"Performance\"\n                                type=\"gradient-info\"\n                                sub-title=\"49,65%\"\n                                icon=\"ni ni-chart-bar-32\"\n                                class=\"mb-4\">\n\n                        <template slot=\"footer\">\n                            <span class=\"text-success mr-2\">54.8%</span>\n                            <span class=\"text-nowrap\">Since last month</span>\n                        </template>\n                    </stats-card>\n                </b-col>\n            </b-row>\n        </base-header>\n\n        <!--Charts-->\n        <b-container fluid class=\"mt--7\">\n            <b-row>\n                <b-col xl=\"8\" class=\"mb-5 mb-xl-0\">\n                    <card type=\"default\" header-classes=\"bg-transparent\">\n                        <b-row align-v=\"center\" slot=\"header\">\n                            <b-col>\n                                <h6 class=\"text-light text-uppercase ls-1 mb-1\">Overview</h6>\n                                <h5 class=\"h3 text-white mb-0\">Sales value</h5>\n                            </b-col>\n                            <b-col>\n                                <b-nav class=\"nav-pills justify-content-end\">\n                                    <b-nav-item\n                                        class=\"mr-2 mr-md-0\"\n                                        :active=\"bigLineChart.activeIndex === 0\"\n                                        link-classes=\"py-2 px-3\"\n                                        @click.prevent=\"initBigChart(0)\">\n                                        <span class=\"d-none d-md-block\">Month</span>\n                                        <span class=\"d-md-none\">M</span>\n                                    </b-nav-item>\n                                    <b-nav-item\n                                        link-classes=\"py-2 px-3\"\n                                        :active=\"bigLineChart.activeIndex === 1\"\n                                        @click.prevent=\"initBigChart(1)\"\n                                    >\n                                        <span class=\"d-none d-md-block\">Week</span>\n                                        <span class=\"d-md-none\">W</span>\n                                    </b-nav-item>\n                                </b-nav>\n                            </b-col>\n                        </b-row>\n                        <!--<line-chart\n                            :height=\"350\"\n                            ref=\"bigChart\"\n                            :chart-data=\"bigLineChart.chartData\"\n                            :extra-options=\"bigLineChart.extraOptions\"\n                        >\n                        </line-chart>-->\n                    </card>\n                </b-col>\n\n                <b-col xl=\"4\" class=\"mb-5 mb-xl-0\">\n                    <card header-classes=\"bg-transparent\">\n                        <b-row align-v=\"center\" slot=\"header\">\n                            <b-col>\n                                <h6 class=\"text-uppercase text-muted ls-1 mb-1\">Performance</h6>\n                                <h5 class=\"h3 mb-0\">Total orders</h5>\n                            </b-col>\n                        </b-row>\n\n                      <!--<bar-chart\n                          :height=\"350\"\n                          ref=\"barChart\"\n                          :chart-data=\"redBarChart.chartData\"\n                      >\n                      </bar-chart>-->\n                  </card>\n              </b-col>\n          </b-row>\n          <!-- End charts-->\n\n            <!--Tables-->\n            <b-row class=\"mt-5\">\n                <b-col xl=\"8\" class=\"mb-5 mb-xl-0\">\n                    <page-visits-table></page-visits-table>\n                </b-col>\n                <b-col xl=\"4\" class=\"mb-5 mb-xl-0\">\n                    <social-traffic-table></social-traffic-table>\n                </b-col>\n            </b-row>\n            <!--End tables-->\n        </b-container>\n\n    </div>\n</template>\n\n<script>\n// Charts\nimport * as chartConfigs from '@/argon/components/Charts/config'\n//import LineChart from '@/argon/components/Charts/LineChart'\n//import BarChart from '@/argon/components/Charts/BarChart'\n\n// Components\nimport BaseProgress from '@/argon/components/BaseProgress'\nimport StatsCard from '@/argon/components/Cards/StatsCard'\n\n// Tables\nimport SocialTrafficTable from '../argon/views/Dashboard/SocialTrafficTable'\nimport PageVisitsTable from '../argon/views/Dashboard/PageVisitsTable'\n\nimport DashboardLayout from '../argon/views/Layout/DashboardLayout'\n\nexport default {\n    layout: (h, page) => h(DashboardLayout, [page]),\n    components: {\n        //LineChart,\n        //BarChart,\n        BaseProgress,\n        StatsCard,\n        PageVisitsTable,\n        SocialTrafficTable\n    },\n    data() {\n        return {\n            bigLineChart: {\n                allData: [\n                    [0, 20, 10, 30, 15, 40, 20, 60, 60],\n                    [0, 20, 5, 25, 10, 30, 15, 40, 40]\n                ],\n                activeIndex: 0,\n                chartData: {\n                    datasets: [\n                        {\n                            label: 'Performance',\n                            data: [0, 20, 10, 30, 15, 40, 20, 60, 60],\n                        }\n                    ],\n                    labels: ['May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],\n                },\n                extraOptions: chartConfigs.blueChartOptions,\n            },\n            redBarChart: {\n                chartData: {\n                    labels: ['Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],\n                    datasets: [{\n                        label: 'Sales',\n                        data: [25, 20, 30, 22, 17, 29]\n                    }]\n                },\n                extraOptions: chartConfigs.blueChartOptions\n            }\n        }\n    },\n    methods: {\n        initBigChart(index) {\n            let chartData = {\n                datasets: [\n                    {\n                        label: 'Performance',\n                        data: this.bigLineChart.allData[index]\n                    }\n                ],\n                labels: ['May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],\n            }\n            this.bigLineChart.chartData = chartData\n            this.bigLineChart.activeIndex = index\n        }\n    },\n    mounted() {\n        this.initBigChart(0)\n    }\n}\n</script>\n\n<style>\n.el-table .cell {\n    padding-left: 0px;\n    padding-right: 0px;\n}\n</style>\n"],"sourceRoot":""}]);
